<template>
 <div class="home">
  <button class="btn btn-primary" @click="abreCadastro">Cadastro</button>

  <!-- Modal -->
  <div
   class="modal fade"
   id="modalCadastro"
   tabindex="-1"
   aria-labelledby="modalCadastroLabel"
   aria-hidden="true"
  >
   <div class="modal-dialog">
    <div class="modal-content">
     <div class="modal-header">
      <h1 class="modal-title fs-5" id="modalCadastroLabel">Cadastro</h1>
      <button
       type="button"
       class="btn-close"
       data-bs-dismiss="modal"
       aria-label="Close"
      ></button>
     </div>
     <div class="modal-body text-start">
      <div class="mb-3">
       <label for="nomeinput" class="form-label">Nome</label>
       <input type="text" class="form-control" id="nomeinput" v-model="item.nome" />
      </div>
      <div class="mb-3">
       <label for="telefoneinput" class="form-label">Telefone</label>
       <input type="tel" class="form-control" id="telefoneinput" v-model="item.telefone" />
      </div>
      <div class="mb-3">
       <label for="emailinput" class="form-label">E-mail</label>
       <input type="email" class="form-control" id="emailinput" v-model="item.email" />
      </div>
     </div>
     <div class="modal-footer">
      <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">
       Fechar
      </button>
      <button type="button" class="btn btn-primary" @click="salva">
       Salvar
      </button>
     </div>
    </div>
   </div>
  </div>

  <table class="table">
   <thead>
    <tr>
     <th scope="col" class="text-start" style="width: 50%">Nome</th>
     <th scope="col" style="width: 25%">Telefone</th>
     <th scope="col">E-mail</th>
    </tr>
   </thead>
   <tbody>
    <tr v-for="(i, index) in lista" :key="index">
     <td class="text-start">{{ i.nome }}</td>
     <td>{{ i.telefone }}</td>
     <td>{{ i.email }}</td>
    </tr>
   </tbody>
  </table>
 </div>
</template>

<script>
import { Modal } from "bootstrap";

export default {
 name: "HomeView",
 components: {},
 data() {
  return {
   modalCadastro: null,
   item: {
    nome: "",
    telefone: "",
    email: "",
   },
   lista: [],
  };
 },
 mounted() {
  this.modalCadastro = new Modal("#modalCadastro", {});
  console.log(this.modalCadastro);
 },
 methods: {
  abreCadastro() {
   this.modalCadastro.show();
  },
  salva() {
   this.lista.push(this.item);
   this.item = {
    nome: "",
    telefone: "",
    email: "",
   };
   this.modalCadastro.hide();
  },
 },
};
</script>
